body {
  margin: 0;
  position: relative;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--navy);
  overflow-x: hidden;
  overflow-y: scroll;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  
    monospace;
}

* {
  box-sizing: border-box;
}

:root {
  --orange: rgb(214, 164, 37);
  --lightorange: rgb(233, 203, 112);
  --navy: #011627;
  --lightNavy: rgb(2, 36, 63);
  --lighterNavy: rgb(14, 64, 104);
  --white: #CCD6f6;
  --gray: #8892B0;
  --dark:#484d5c;
  --black:#010d17;
}

